import React, { Component, lazy } from 'react'
import Select from 'react-select';
import {
  CBadge,
  CDataTable,
  CFormGroup,
  CLabel,
  CInput,
  CButton,
  CForm,
  CCard,
  CCardBody,
  CCardFooter,
  CCardHeader,
  CInputCheckbox,
  CCol,
  CRow,
} from '@coreui/react'
import {fields, jns_manifest, jns_moda, datakpbcs, kelpos } from './Datasource.js'
import Detail from './Detail.js'
import Editmaster from './Editmaster.js'
import Hapusmaster from './Hapusmaster.js'
class NVOCC extends Component {
  constructor(props) {
    super(props);
    this.ref = React.createRef()
    this.state = {
      datamasterhost:[],
      nopos:0,
      kelpos: null,
      nomaster:'',
      value: null,
      tglmaster:'',
      jmlhouse:'',
      posbc11:'',
      respon:'',
      status:'',
      hostbl:0,
      jns_moda: null,
      id:0,
      noaju: '1234567890',
      disabled: true
    };
  }
      handleChange (event) {
        const nama = event.target.name;
        this.setState({
          [nama] : event.target.value
        })
      }
      handleReactSelect = (selectedOption, { name }) => {
        if((name == "jns_manifest") && (selectedOption.value == "01" || selectedOption.value == "03")) {
            this.setState({ [name]: selectedOption.value, jns_moda : jns_moda[1]});  
          } else if ((name == "jns_manifest") && (selectedOption.value == "02" || selectedOption.value == "04")) {
            this.setState({ [name]: selectedOption.value, jns_moda : jns_moda[0]});
          } else {
            this.setState({ [name]: selectedOption.value, value: selectedOption });
          }
        }
      handleClick (){
        if (this.state.kelpos != '' && this.state.nomaster != '' && this.state.tglmaster != '' && this.state.jmlhouse != '') {
        this.setState( prevState => ({
          datamasterhost:[...prevState.datamasterhost, {id:this.state.id, kelposvalue: this.state.value, kelpos: this.state.kelpos, nomaster: this.state.nomaster, tglmaster: this.state.tglmaster, jmlasumsihouse: parseInt(this.state.jmlhouse), jmlhouse: this.state.hostbl, posbc11: '', respon: '' , status: 'Tidak Lengkap'}],
          id: this.state.id + 1,
          kelpos : null,
          nomaster:'',
          tglmaster:'',
          jmlhouse:'',
          value:null
        }),
        )          
      } else {
        alert("Harap Dilengkapi dulu formnya")
      }
      }
      handleOption = (e) => {
        if (e.target.checked) {
          this.setState ({disabled:false})
        } else {
          this.setState ({disabled:true})
        }
      }    
      setjmlAktualHouse = (parentid, jmlaktualhouse) => {
        const index = this.state.datamasterhost.findIndex(item => item.id == (parentid-1))
        var datamasterhostkopian : []
        datamasterhostkopian = this.state.datamasterhost
        datamasterhostkopian[index].jmlhouse = jmlaktualhouse
        if(datamasterhostkopian[index].jmlasumsihouse == jmlaktualhouse) {
          datamasterhostkopian[index].status = 'Tidak Lengkap'
        } else {
          datamasterhostkopian[index].status = 'Tidak Lengkap'
        }
        this.setState(prevState => ({...prevState, datamasterhost: datamasterhostkopian}))
      }
  render(){
      const Getbadge = status => {
        switch (status) {
          case 'Lengkap': return 'success'
          case 'Tidak Lengkap': return 'danger'
        }
      }

      console.log("status state", this.state.id)
      console.log("status nomaster", this.state)
      console.log("status aktua house", this.state.hostbl)  
    return(
      <CCard>
        <CCardHeader className="bg-light text-dark" >
          <h5>Entry Manifest</h5>
        </CCardHeader>      
        <CCardBody>
          <CRow>
            <CCol xs="12" sm="12">
              <CCard>
                <CCardHeader className="bg-light text-dark">
                  <h5>Data Header</h5>
                </CCardHeader>
                <CCardBody>
                  <CRow>
                    <CCol sm="6">
                      <CRow>
                        <CCol xs="12">
                          <CFormGroup>
                            <CLabel htmlFor="noaju">No. Pengajuan</CLabel>
                            <CInput id="No Pengajuan" value={this.state.noaju} disabled />
                          </CFormGroup>
                        </CCol>
                      </CRow>
                      <CRow>
                        <CCol xs="12">
                          <CFormGroup>
                            <CLabel htmlFor="kpbc">Kantor Pelayanan Bea Cukai</CLabel>
                            <Select name="kpbc" id="select" options={datakpbcs} />
                          </CFormGroup>
                        </CCol>
                      </CRow>
                      <CRow>
                        <CCol xs="12">
                          <CFormGroup>
                            <CLabel htmlFor="jns_manifest">Jenis Manifest</CLabel>
                            <Select  onChange={this.handleReactSelect.bind(this)} name="jns_manifest" id="select" options={jns_manifest} />
                          </CFormGroup>
                        </CCol>
                      </CRow>
                      <CRow>
                        <CCol xs="12">
                          <CFormGroup>
                            <CLabel htmlFor="jns_moda">Jenis Moda</CLabel>
                            <Select value={this.state.jns_moda} name="jns_moda" id="select" isDisabled options={jns_moda} />
                          </CFormGroup>
                        </CCol>
                      </CRow>
                      <CRow>
                          <CCol col="6" sm="4" md="2" xl className="mb-3 mb-xl-0">
                            <CButton block color="info">Simpan</CButton>
                          </CCol> 
                        </CRow>                      
                    </CCol>
                    <CCol sm="6">
                        <CRow>
                          <CCol xs="12">
                            <CFormGroup>
                              <CLabel htmlFor="nama_angkut">Nama Sarana Angkut</CLabel>
                              <CInput id="nama_angkut" placeholder="Masukkan Nama Sarana Angkut" required />
                            </CFormGroup>
                          </CCol>
                        </CRow>
                        <CRow>
                          <CCol xs="6">
                            <CFormGroup>
                              <CLabel htmlFor="no_voyage">Nomor Voyage</CLabel>
                              <CInput className="form-control-sx" id="no_voyage" placeholder="Masukkan Nomor Voyage" required />
                            </CFormGroup>
                          </CCol>
                          <CCol xs="6">
                            <CFormGroup>
                              <CLabel htmlFor="ccnumber">Nomor IMO</CLabel>
                              <CInput className="form-control-sx" id="no_imo" placeholder="Masukkan Nomor IMO" required />
                            </CFormGroup>
                          </CCol>                    
                        </CRow>
                        <CRow>
                          <CCol xs="6">
                            <CFormGroup>
                              <CLabel htmlFor="tgl_tiba">Tanggal Tiba</CLabel>
                              <CInput className="form-control-sx" type="date" id="tgl_tiba" name="tgl_tiba" placeholder="date" />
                            </CFormGroup>
                          </CCol>
                          <CCol xs="6">
                            <CFormGroup>
                              <CLabel htmlFor="jam_tiba">Jam Tiba</CLabel>
                              <CInput className="form-control-sx" type="time" id="jam_tiba" name="jam_tiba" placeholder="time" step="2" />
                            </CFormGroup>
                          </CCol>
                        </CRow>
                        <CRow>
                          <CCol xs="12">
                            <CFormGroup>
                              <CLabel htmlFor="pemberitahu">Pemberitahu</CLabel>
                              <CInput className="form-control-sx" id="pemberitahu" placeholder="Masukkan Pemberitahu" required />
                            </CFormGroup>
                          </CCol>                    
                        </CRow>
                        <CRow>
                          <CCol col="6" sm="4" md="2" xl className="mb-3 mb-xl-0">
                            <CButton block color="info">Cek Rekon</CButton>
                          </CCol> 
                        </CRow>
                    </CCol>
                  </CRow>                                    
                </CCardBody>
              </CCard>
            </CCol>
          </CRow>
          <CRow>
            <CCol xs="12" sm="12">
              <CCard>
                <CCardHeader className="bg-light text-dark">
                  <h5>Daftar Master Host BL</h5>
                </CCardHeader>
                <CCardBody>
                  <CRow>
                    <CCol xs="12">
                    <div ref={this.ref}>
                      <CDataTable
                        items={this.state.datamasterhost}
                        fields={fields}
                        itemsPerPage={5}
                        pagination
                        scopedSlots = {
                          {
                          'status':
                            (item)=>(
                              <td>
                                <CBadge color={Getbadge(item.status)}>
                                  {item.status}
                                </CBadge>
                              </td>
                            ),
                            'jmlhouse':
                            (item, index)=> {
                              var filteran=this.state.datamasterhost.filter(item => item.id == index)
                              var filterann=filteran[0].jmlhouse
                              var bingungnama=this.state.datamasterhost.filter(item => item.id == index)
                              var bingungnaman=filteran[0].jmlasumsihouse                             
                              return (
                                <td>
                                  {filterann} dari {bingungnaman} 
                                </td>
                              )
                            },
                            'action' :
                            (item,index) => {
                              var nomaster=this.state.datamasterhost.filter(item => item.id == index)
                              var nomastern=nomaster[0].nomaster
                              var tglmaster=this.state.datamasterhost.filter(item => item.id == index)
                              var tglmastern=tglmaster[0].tglmaster
                              var kelpos=this.state.datamasterhost.filter(item => item.id == index)
                              var kelposn=kelpos[0].kelpos
                              var jmlasumsihouse=this.state.datamasterhost.filter(item => item.id == index)
                              var jmlasumsihousesn=jmlasumsihouse[0].jmlasumsihouse
                              var kelposvalue=this.state.datamasterhost.filter(item => item.id == index)
                              var kelposvaluen=kelposvalue[0].kelposvalue
                              console.log("kelposvaluen", kelposvaluen)
                              return (
                                <td>
                                <CRow>
                                  <div className="pl-1">
                                    <Detail idmaster={index+1} nomaster={nomastern} tglmaster={tglmastern} kelpos={kelposn} noaju={this.state.noaju} jmlhouse={jmlasumsihousesn} calleback={this.setjmlAktualHouse} />
                                  </div>
                                  <div className="pl-1">
                                    <Editmaster kelpos={kelposvaluen} nomaster={nomastern} tglmaster={tglmastern} jmlasumsihouse={jmlasumsihousesn} />
                                  </div> 
                                  <div className="pl-1">
                                    <Hapusmaster/>
                                  </div>                                                                    
                                </CRow>
                                </td>
                                )
                            },
                            'no' :
                            (item,index) => {
                              return (
                                <td>
                                {index + 1}
                                </td>
                                )
                            }                                                        
                          }                         
                      }                        
                      />
                      </div>
                      </CCol>
                  </CRow>                                                    
                </CCardBody>
              </CCard>
            </CCol>
          </CRow> 
          <CRow>
            <CCol xs="12" sm="12">
              <CCard>
                <CCardHeader className="bg-light text-dark">
                  <h5>Tambah Master Host BL</h5>
                </CCardHeader>
                <CCardBody>
                  <CForm id="tambahmasterhost">
                    <CRow>
                      <CCol xs="12">
                        <CFormGroup>
                          <CLabel htmlFor="kelpos">Kelompok Pos</CLabel>
                          <Select value={this.state.value} onChange={this.handleReactSelect.bind(this)} name="kelpos" id="select" options={kelpos} />
                        </CFormGroup>
                      </CCol>
                    </CRow>
                    <CRow>
                      <CCol xs="6">
                        <CFormGroup>
                          <CLabel htmlFor="nomaster">No. Master Host BL</CLabel>
                          <CInput value ={this.state.nomaster} onChange={this.handleChange.bind(this)} name="nomaster" placeholder="Masukkan No Master Host BL" />
                        </CFormGroup>
                      </CCol>
                      <CCol xs="6">
                        <CFormGroup>
                          <CLabel htmlFor="tglmaster">Tgl Master Host BL</CLabel>
                          <CInput value={this.state.tglmaster} onChange={this.handleChange.bind(this)} type="date" name="tglmaster" id="tglmaster" placeholder="date" />
                        </CFormGroup>
                      </CCol>
                    </CRow>
                    <CRow>
                      <CCol xs="12">
                        <CFormGroup>
                          <CLabel htmlFor="jml_house">Jumlah House BL</CLabel>
                          <CInput value={this.state.jmlhouse} onChange={this.handleChange.bind(this)} name="jmlhouse" placeholder="Masukkan Jumlah House"  />
                        </CFormGroup>
                      </CCol>
                    </CRow>
                    <CRow>
                      <CCol col="6" sm="4" md="2" xl className="mb-3 mb-xl-0">
                        <CButton onClick={this.handleClick.bind(this)} block color="info">Tambah</CButton>
                      </CCol> 
                    </CRow>
                  </CForm>                                                       
                </CCardBody>
              </CCard>
            </CCol>
          </CRow>                   
        </CCardBody>
        <CCardFooter>
        <div className="d-flex justify-content-center">
          <CRow>
            <CCol md sm="12" className="mb-sm-2 mb-0">
                  <CFormGroup variant="custom-checkbox" inline>
                    <CInputCheckbox 
                      custom 
                      id="inline-checkbox1" 
                      name="inline-checkbox1" 
                      value="option1"
                      onChange={this.handleOption.bind(this)} 
                    />
                    <CLabel variant="custom-checkbox" htmlFor="inline-checkbox1"> Saya Menyatakan Bahwa Data yang Saya akan Kirimkan adalah benar dan Dapat Dipertanggungjawabkan</CLabel>
                  </CFormGroup>            
            </CCol>
          </CRow>
        </div>
        <div className="d-flex flex-row justify-content-center">
          <CRow>
            <div className="p-2"><CButton color="info" disabled={this.state.disabled} >Kirim</CButton></div>
            <div className="p-2"><CButton color="danger">Batal</CButton></div>                  
          </CRow>
        </div>
        </CCardFooter>
      </CCard>
      )
  }  
}

export default NVOCC
