import React, { useState } from 'react'
import Select from 'react-select';
import {
  CButton,
  CFormGroup,
  CLabel,
  CInput,
  CCol,
  CModal,
  CModalBody,
  CModalFooter,
  CModalHeader,
  CModalTitle,
  CRow
} from '@coreui/react'

import {fieldhostbl, jns_manifest, jns_moda, datakpbcs, kelpos, pelabuhan, kodehs } from './Datasource.js'

const Editmaster = (props) => {

  const [modal, setModal] = useState(false)
  const [value, setValue] = useState(props.kelpos)
  const handleReactSelect = (selectedOption, { name }) => {
    setValue(selectedOption);
  }
  console.log("iki bos", props.kelpos)
  return (
    <>
      <CButton 
        onClick={() => {
            setModal(!modal)
        }}
        color="success"
        size="sm"
      >
        <a>Edit</a>
      </CButton>
      <CModal 
        show={modal} 
        onClose={setModal}
        size="l"
        closeOnBackdrop={false}
      >
        <CModalHeader closeButton>
          <CModalTitle>Edit Master Host</CModalTitle>
        </CModalHeader>
        <CModalBody>
          <CRow>
            <CCol sm="12">
                <CRow>
                <CCol sm="12">
                    <CFormGroup>
                    <CLabel htmlFor="kodehs">Kelompok Pos</CLabel>
                    <Select value={value} onChange = {handleReactSelect.bind(this)} name ="kodehs" options={kelpos} isDisabled/>
                    </CFormGroup>
                </CCol>
                </CRow>
                <CRow>
                    <CCol xs="6">
                    <CFormGroup>
                        <CLabel htmlFor="nomaster">No. Master Host BL</CLabel>
                        <CInput value={props.nomaster} name="nomaster" placeholder="Masukkan No Master Host BL" />
                    </CFormGroup>
                    </CCol>
                    <CCol xs="6">
                    <CFormGroup>
                        <CLabel htmlFor="tglmaster">Tgl Master Host BL</CLabel>
                        <CInput value={props.tglmaster}type="date" name="tglmaster" id="tglmaster" placeholder="date" />
                    </CFormGroup>
                    </CCol>    
                </CRow>  
                <CRow>
                <CCol sm="12">
                    <CFormGroup>
                    <CLabel htmlFor="nomaster">Jumlah House BL</CLabel>
                    <CInput value={props.jmlasumsihouse} name="nomaster" placeholder="Masukkan No Master Host BL" />
                    </CFormGroup>
                </CCol>
                </CRow>            
            </CCol>
          </CRow>                                                                       
        </CModalBody>
        <CModalFooter>
          <CButton color="primary">Simpan</CButton>{' '}
          <CButton 
            color="secondary" 
            onClick={() => setModal(false)}
          >
            Batal
          </CButton>
        </CModalFooter>
      </CModal>
    </>
  )
}    

export default Editmaster